// 	background-image: none;
// 	display: block;
// 	width: auto;
// 	height: auto;
//   }
  
  .carousel-control-prev-icon svg, .carousel-control-next-icon svg {
	width: 48px;
	height: 48px;
  }
  
  .carousel-indicators li {
	width: 10px !important;
	height: 10px !important;
	border-radius: 50%;
  }
  .carousel-item-background {
    content: '';
    background: rgba(0, 0, 0, 0.5);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

  /*------ Carousel -------*/