@media (min-width: 992px) {
  .app.sidebar-mini.sidenav-toggled .profile-status {
    inset-inline-end: 8px;
    top: 44px;
  }

  .app.sidenav-toggled .side-menu__label {
    display: none !important;
    position: relative;
    padding: 0;
    margin: 0;
