.slide-item {
  &.active,
  &:hover,
  &:focus {
    text-decoration: none;
    color: $primary !important;

    &::before {
      color: $primary !important;
    }
    .sub-angle {
      color: $primary !important;
      opacity: 1;
    }
  }
}
.sub-side-menu__item {
  &.active,
  &:hover,
  &:focus {
    text-decoration: none;
    color: $primary !important;

    &::before {
      color: $primary !important;
    }
    .sub-angle {
      color: $primary !important;
      opacity: 1;
    }
  }
}

.slide-menu {
  .sub-slide-menu {
    padding: 0 5px;
  }
}

.sub-slide .sub-angle {
  margin-inline-start: auto;
  float: $float-right;
  font-size: 12px;
  margin-block-start: 3px;
}

.slide-menu li {
  position: relative;
}

.slide.is-expanded {
  a {
    color: #6d7790;
    text-decoration: none;
  }

  .sub-side-menu__item:before {
    content: "\e0e7";
    font-family: "typicons" !important;
    position: absolute;
    top: 9px;
    inset-inline-start: 20px;
    font-size: 9px;
  }
}

.side-menu .side-menu__icon {
  font-size: 25px;
    line-height: 30px;
    text-align: center;
    vertical-align: middle;
    margin-inline-end: 12px;
    width: 37.5px;
    height: 37.5px;
    background: #fff;
    padding: 8px;
    border-radius: 50px;
    box-shadow: 0px 2px 3px rgba(4, 4, 7, 0.1);
    border: 1px solid #ebecf1;
}

.side-menu__icon {
  -webkit-box-flex: 0;
  -ms-flex: 0 0 auto;
  flex: 0 0 auto;
  width: 25px;
}

.side-menu__label {
  white-space: nowrap;
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  color: #5b6e88;
  position: relative;
  font-size: 13.5px;
  line-height: 1;
  vertical-align: middle;
  font-weight: 400;
}

.app-sidebar .side-item.side-item-category {
  color: #2c364c;
  font-size: 11px;
  text-transform: uppercase;
  font-weight: 700;
