/*------ Breadcrumb ------*/

.breadcrumb {
	display: flex;
	flex-wrap: wrap;
	margin-bottom: 0;
	list-style: none;
	border-radius: 3px;
	font-weight: 400;
	background: #f0f0f2;
	padding: 0.75rem 1rem;
  }
  
  .breadcrumb-item {
	+ .breadcrumb-item {
	  &::before {
		display: inline-block;
		padding-inline-end: 0.5rem;
		padding-inline-start: 0.5rem;
		color: #cfd1e1;
		content: "/";
		opacity: 0.5;
	  }
  
	  &:hover::before {
		text-decoration: underline;
		text-decoration: none;
	  }
	}
  
	&.active {
	  color:$default-color;
	}
  }
  
  .breadcrumb1 {
	display: flex;
	flex-wrap: wrap;
	padding: .75rem 1rem;
	margin-bottom: 1rem;
	list-style: none;
	background-color: #f0f0f2;
	border-radius: .25rem;
  }
  
  .breadcrumb-item1 {
	a:hover {
	  color: #123787;
	  text-decoration: underline;
	}
  
	+ .breadcrumb-item1 {
	  &::before {
		display: inline-block;
		padding-inline-end: 0.5rem;
		padding-inline-start: 0.5rem;
		content: "/";
		opacity: 0.5;
	  }
  
	  &:hover::before {
		text-decoration: underline;
		text-decoration: none;
	  }
	}
  
	&.active {
	  color:$default-color;
	}
  }
  
  .breadcrumb-arrow {
	height: 36px;
	padding: 0;
	line-height: 36px;
	list-style: none;
	background-color: #f0f0f2;
  
	li {
	  &:first-child a {
		border-radius: 4px 0 0 4px;
		-webkit-border-radius: 4px 0 0 4px;
		-moz-border-radius: 4px 0 0 4px;
		padding: 0 10px;
		padding-inline-start: 10px;
	  }
  
	  display: inline-block;
	  vertical-align: top;
  
	  a, span {
		display: inline-block;
		vertical-align: top;
	  }
  
	  &:not(:first-child) {
		margin-inline-start: -5px;
	  }
  
	  + li:before {
		padding: 0;
		content: "";
	  }
  
	  span {
		padding: 0 10px;
		color:$default-color;
	  }
  
	  a, &:not(:first-child) span {
		height: 36px;
		padding-block-start: 0px;
  	    padding-inline-end: 10px;
        padding-block-end: 0px;
        padding-inline-start: 25px;
		line-height: 36px;
	  }
  
	  a {
		position: relative;
		color: $white;
		text-decoration: none;
  
		&:after {
		  position: absolute;
		  top: -1px;
		  width: 0;
		  height: 0;
		  content: '';
		  border-block-start: 18px solid transparent;
		  border-block-end: 18px solid transparent;
		}
  
		&:before {
		  position: absolute;
		  top: -1px;
		  width: 0;
		  height: 0;
		  content: '';
		  border-block-start: 18px solid transparent;
		  border-block-end: 18px solid transparent;
		  inset-inline-end: -11px;
		  z-index: 3;
		  border-inline-start-style: solid;
		  border-inline-start-width: 11px;
		}
  
		&:after {
		  inset-inline-end: -12px;
		  z-index: 2;
		  border-inline-start: 11px solid $primary;
		}
	  }
	}
  }
  
  @media (max-width: 375px) {
	.breadcrumb {
	  font-size: 12px;
	}
  
	.page-title {
	  font-size: 20px !important;
	}
  }
  
  .breadcrumb-style2 .breadcrumb-item + .breadcrumb-item::before, .breadcrumb-style3 .breadcrumb-item + .breadcrumb-item::before {
	width: 25px;
	text-align: center;
	font-family: 'feather';
	line-height: 20px;
  }
  
  .breadcrumb-style2 .breadcrumb-item + .breadcrumb-item::before {
	content: '\e92f';
  }
  
  .breadcrumb-style3 .breadcrumb-item + .breadcrumb-item::before {
	content: '\e9d3';
  }
  
  .breadcrumb-style4 {
	.breadcrumb-icon {
	  margin-block-start: 10px;
	  margin-inline-start: 4px;
	}
  
	.breadcrumb-item {
	  .svg-icon {
		width: 20px;
	  }
  
	  &:first-child {
		margin-block-start: 0;
	  }
  
	  margin-block-start: 3px;
	}
  }
  
  .svg-breadcrumb.breadcrumb-style4 .breadcrumb-item + .breadcrumb-item::before {
	margin-block-start: 8px;
	padding: 0 12px;
  }
  
  .breadcrumb-arrow li a {
	background-color: $primary;
	border: 1px solid $primary;
  
	&:before {
	  border-inline-start-color: $primary;
	}
  }
  .page-leftheader .breadcrumb-item.active a{
	color: $primary !important;
  }
  
  /*------ Breadcrumbs -------*/