.btn-animation .btn-loaders:after {
  content: "";
  -webkit-animation: loader 500ms infinite linear;
  animation: loader 500ms infinite linear;
  border: 2px solid $white;
  border-radius: 50%;
  border-inline-end-color: transparent !important;
  border-block-start-color: transparent !important;
  display: block;
  height: 1.4em;
  width: 1.4em;
  position: absolute;
  inset-inline-end: 10px !important;
  top: calc(50% - 0.7em);
  -webkit-transform-origin: center;
  transform-origin: center;
  position: absolute !important;
}

.btn-animation .btn-spiner:after {
  content: "";
  -webkit-animation: sk-bounce 1.2s infinite ease-in-out;
  animation: sk-bounce 1.2s infinite ease-in-out;
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
  border-radius: 50%;
  border-inline-end-color: transparent !important;
  border-block-start-color: transparent !important;
  display: block;
  height: 0.5em;
  width: 0.5em;
  position: absolute;
  inset-inline-end: 12px !important;
  top: calc(50% - 0.7em);
  -webkit-transform-origin: center;
  transform-origin: center;
  position: absolute !important;
  background: $white;
  width: 14%;
  height: 45%;
}

.btn-animation .btn-loaders {
  color: $white  !important;
  pointer-events: none;
  position: relative;
}

.btn-animation .btn-spiner {
  color: $white  !important;
  pointer-events: none;
  position: relative;
}

.btn-animation .loading {
  margin-inline-end: 30px;
}

@-webkit-keyframes loader {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes loader {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@-webkit-keyframes sk-bounce {

  0%,
  100% {
    -webkit-transform: scale(0);
  }

  50% {
    -webkit-transform: scale(1);
  }
}

@keyframes sk-bounce {

  0%,
  100% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }

  50% {
    transform: scale(1);
    -webkit-transform: scale(1);
  }
}

